$tablet-width: 768px;
$desktop-width: 1024px;

@mixin mobile {
    @media (max-width: #{$tablet-width - 1px}) {
        @content; } }

@mixin tablet {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
        @content; } }

@mixin desktop {
    @media (min-width: #{$desktop-width}) {
        @content; } }

$page-max-width: 1440px;

%page-center {
    max-width: $page-max-width;
    margin-left: auto;
    margin-right: auto; }
